/*Fonts*/
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');
/*Fonts*/


/*Mixin*/
@mixin flex($direction, $content, $items, $wrap) {
	display: flex;

	@if $direction {
		flex-direction: $direction;
	}

	@if $content {
		justify-content: $content;
	}

	@if $items {
		align-items: $items;
	}

	@if $wrap {
		flex-wrap: $wrap;
	}
}
/*Mixin*/
/*Styles*/
/*Basic*/

* {
	margin: 0;
	padding: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
}

html {height:100%;}

body, html {
	width: 100%;
	color: #000;
}

body {
	position: relative;
	margin: 0;
	padding: 0;
	min-height: 100%;
	background-color: #f5f8f9;

	* {
		font-family: 'Montserrat', sans-serif;
		color: #0d2a4a;
	}

	&.overlay {
		overflow: hidden;
	}
}

input,
button {
	border: 0 none;
	background-color: transparent;
}

a:focus,
input:focus,
button:focus {
	outline: none;
}

button {
	cursor: pointer;
}

.logo {
	display: block;
  width: 200px;
  height: 243px;
	background-image: url("../img/logo.png");
	background-image: -webkit-image-set(
    url("../img/logo.png") 1x,
    url("../img/logo@2x.png") 2x,
    url("../img/logo@3x.png") 3x,
  );
	background-image: image-set(
    url("../img/logo.png") 1x,
    url("../img/logo@2x.png") 2x,
    url("../img/logo@3x.png") 3x,
  );
  background-repeat: no-repeat;		

	&--small {
		display: inline-block;
		width: 61px;
	  height: 61px;
		background-image: url("../img/logo_small.png");
		background-image: -webkit-image-set(
	    url("../img/logo_small.png") 1x,
	    url("../img/logo_small@2x.png") 2x,
	    url("../img/logo_small@3x.png") 3x,
	  );
		background-image: image-set(
	    url("../img/logo_small.png") 1x,
	    url("../img/logo_small@2x.png") 2x,
	    url("../img/logo_small@3x.png") 3x,
	  );
	  background-repeat: no-repeat;			
	}
}

.avatar {
	position: relative;
	font-size: 0;
	line-height: 0;
	border-radius: 50%;
	overflow: hidden;

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		-o-object-fit: cover;
		object-fit: cover;
		-o-object-position: center;
		object-position: center;
	}
}

/*Forms*/
.form {
	&__row {
		margin-bottom: 32px;

		&--columns {
			@include flex(false, space-between, start, false);

			.columns__column {
				&--course {
					flex: 1;
				}

				&--start,
				&--end {
					padding-left: 32px;
					flex: 0 0 287px;
					width: 287px;
				}

				&--time,
				&--doze {
					margin-left: 32px;
					flex: 1;
				}
			}
		}

		&.tac {
			text-align: center;
		}
	}

	&__label {
		display: block;
		margin-bottom: 17px;
		font-size: 18px;
		font-weight: 600;
		line-height: 1.5;
		letter-spacing: 0.11px;
		cursor: pointer;

		&--gallery {
			display: block;

			&:before {
				display: inline-block;
				content: '';
				width: 85px;
				height: 85px;
				cursor: pointer;
				border-radius: 5px;
				background-color: rgba(43, 132, 235, 0.1);
				background-image: url("../img/icons/i-camera_2.png");
				background-image: -webkit-image-set(
					url("../img/icons/i-camera_2.png") 1x,
					url("../img/icons/i-camera_2@2x.png") 2x,
					url("../img/icons/i-camera_2@3x.png") 3x,
				);
				background-image: image-set(
					url("../img/icons/i-camera_2.png") 1x,
					url("../img/icons/i-camera_2@2x.png") 2x,
					url("../img/icons/i-camera_2@3x.png") 3x,
				);
				background-position: 50%;
				background-repeat: no-repeat;
				vertical-align: middle;			
			}

			span {
				margin-left: 30px;
				font-size: 18px;
				font-weight: 600;
				letter-spacing: 0.11px;
				text-align: left;
				color: #2b84eb;
			}

			input	{
				display: none;
			}
		}

		&--radio {
			display: block;
			font-size: 16px;
			font-weight: 600;
			color: rgba(13,42,74, 0.5);
			cursor: pointer;
			line-height: 1.5;
			letter-spacing: 0.15px;

			&:before {
				display: inline-block;
				content:'';
				margin: -3px 16px 0 0;
				width: 20px;
				height: 20px;
				box-shadow: inset 1px 1px 2px 0 rgba(0, 0, 0, 0.1);
				background-color: #dbdfe4;
				border-radius: 50%;
				vertical-align: middle;
			}
		}
	}

	&__input,
	&__textarea,
	&__select {
		padding: 16px 16px 17px 16px;
		width: 100%;
		font-size: 16px;
		font-weight: 500;
		line-height: 1.5;
		letter-spacing: 0.15px;
		line-height: 1;
		border-radius: 3.5px;
		border: solid 1px #dbdfe4;
		background-color: #ffffff;
		box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);
		outline: none;

		&::-webkit-input-placeholder { 
		  color: rgba(13, 42, 74,0.54);
		}

		&::-moz-placeholder { 
		  color: rgba(13, 42, 74,0.54);
		}

		&:-ms-input-placeholder { 
		  color: rgba(13, 42, 74,0.54);
		}

		&:-moz-placeholder { 
		  color: rgba(13, 42, 74,0.54);
		}		
	}

	&__input {
		&.datepicker-here {
			background: #fff url("../img/icons/i-calendar_2.png") no-repeat calc(100% - 16px) 50%;
		}

		&--radio {
			display: none;

			&:checked {
				& + label {
					color: #0d2a4a;

					&:before {
						width: 10px;
						height: 10px;
						border: solid 5px #2b84eb;
						background-color: #f5f8f9;
						box-shadow: unset;
					}
				}
			}
		}

		&--pin {
			margin: 0 12px;
			width: 20px;
			height: 20px;
			box-shadow: inset 1px 1px 2px 0 rgba(0, 0, 0, 0.1);
			background-color: #dbdfe4;
			border-radius: 50%;
		}
	}

	&__textarea {
		line-height: 1.33;
	}

	.custom-select {
		position: relative;

		select {
			display: none;
		}

		.select {
			&-selected {
				position: relative;
				width: 100%;
				padding: 18px 46px 18px 16px;
				font-size: 16px;
				color: #0d2a4a;
				cursor: pointer;
				line-height: 1;
				border-radius: 4px;
				border: solid 1px #dcdcdc;
				background-color: #ffffff;
				box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);

				&:after {
					content: '';
					position: absolute;
					right: 16px;
					top: 50%;
					margin-top: -6px;
					width: 21px;
					height: 12px;
					background: url("../img/icons/i-chevron_down.png") no-repeat 50%;
				}

				&.select-arrow-active {
					&:after {
						transform: rotate(-180deg);
					}
				}
			}

			&-items {
				position: absolute;
				top: 100%;
				left: 0;
				right: 0;
				padding: 24px;

				background-color: #fff;
				z-index: 21;
				box-shadow: 0 2px 2px 0 rgba(0,0,0,0.2);

				div {
					margin-bottom: 12px;
					font-size: 16px;
					color: #0d2a4a;
					cursor: pointer;

					&:hover {
						color: #000;
					}

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			&-hide {
				display: none;
			}
		}
	}

	.switch {
		&__wrapper {
			@include flex(false, space-between, center, wrap);
			
			&.active {
				.switch__text,
				.switch__text *,
				.switch__hint {
					color: #0d2a4a;

					img {
						opacity: 1;
					}
				}
			}
		}

		&__text,
		&__text * {
			color: rgba(13,42,74, 0.5);

			img {
				opacity: 0.5;
			}
		}

		&__input	{
			display: none;

			&:checked + label {
				background-color: #2b84eb;

				&:before {
					left: calc(100% - 31px);
				}
			}
		}

		&__label {
			position: relative;
			display: block;
			width: 62px;
			height: 34px;
			padding: 2px;
			cursor: pointer;
			border-radius: 75px;
			box-shadow: inset 1px 1px 2px 0 rgba(0, 0, 0, 0.1);
			background-color: #dbdfe4;
			transition: background-color 0.5s ease;

			&:before {
				position: absolute;
				top: 2px;
				left: 2px;
				content: '';
				width: 29px;
				height: 30px;
				border-radius: 75px;
				background-color: #fff;
				transition: left 0.5s ease;
			}
		}

		&__hint {
			flex: 0 0 100%;
			color: rgba(13,42,74, 0.5);
		}
	}

	&__reset,
	&__submit {
		padding: 24px;
		min-width: 250px;
		font-size: 18px;
		font-weight: 600;
		letter-spacing: 0.11px;
		color: #fff;		  
		border-radius: 5px;
		box-shadow: 10px 4px 20px 0 rgba(216, 223, 225, 0.5);

		&:disabled {
			opacity: 0.5;
		}
	}

	&__reset {
		background-color: #ff765f;		
	}

	&__submit {
		background-color: #2b84eb;
	}

	&--authorization {
		width: 430px;

		.logo {
			margin: 0 auto 32px auto;
		}

		.form {
			&__row {
				margin-bottom: 16px;
			}

			&__title {
				margin-bottom: 24px;
				font-size: 36px;
				font-weight: 700;
				line-height: 1.5;
				letter-spacing: 0.22px;
				text-align: center;
			}

			&__row {
				margin-bottom: 16px;

				&--avatar {
					margin-bottom: 32px;
				}

				&--columns {
					@include flex(false, space-betwen, center, false);
					margin: 0 -15px 16px -15px;

					.form__column--2 {
						flex: 0 0 50%;
						padding: 0 15px;
					}
				}

				&--submit {
					margin-bottom: 0;
					padding-top: 24px;
					text-align: center;
				}

				&--skip {
					padding-top: 24px;
					text-align: center;
				}
			}

			&__label {
				display: block;
				margin-bottom: 8px;
				font-size: 18px;
				line-height: 1.5;
				letter-spacing: 0.11px;
				cursor: pointer;

				&--avatar {
					margin: 0 auto;
					width: 200px;
					height: 200px;
					border-radius: 500px;
					background-color: rgba(43, 132, 235, 0.1);
					background-image: url("../img/icons/i-camera.png");
					background-position: 50%;
					background-repeat: no-repeat;
				}
			}

			&__input {
				padding: 16px;
				font-size: 16px;
				font-weight: 600;
				line-height: 1.5;
				letter-spacing: 0.15px;				
				border-radius: 3.5px;
				background-color: #fff;
				border: 0 none;
				box-shadow: unset;

				&--avatar {
					display: none;
				}

				&--datepicker {
					background-image: url("../img/icons/i-calendar.png");
					background-position: calc(100% - 16px) 50%;
					background-repeat: no-repeat;
				}
			}

			&__submit {
				min-width: 332px;
				padding: 16px ;
				font-size: 16px;
				font-weight: 600;
				line-height: 1.5;
				letter-spacing: 0.15px;
				color: #fff;				
				border-radius: 500px;
				background-color: #2b84eb;
			}			
		}

		.btn-skip {
			font-size: 18px;
			line-height: 1.5;
			letter-spacing: 0.11px;
		}		

		.custom-select {
			.select {
				&-selected {
					padding: 16px;
					font-size: 16px;
					font-weight: 600;
					line-height: 1.5;
					letter-spacing: 0.15px;
					color: #0d2a4a;
					border: 0 none;
				}

				&-items {
					right: 15px;
					left: 15px;
					font-size: 16px;
					font-weight: 600;
					line-height: 1.5;
					letter-spacing: 0.15px;	

					div {
						padding: 16px 0;
					}				
				}
			} 
		}		
	}

	&--popup {
		.form {
			&__row {
				margin-bottom: 16px;

				&--columns {
					@include flex(false, space-between, center, false);
					margin: 0 -12px 24px;

					.form__column--4 {
						flex: 0 0 25%;
						padding: 0px 12px;
					}
				}

				&--notification {
					font-size: 18px;
					font-weight: 600;
					line-height: 1.5;
					letter-spacing: 0.11px;
					color: #2b84eb;

					.icon {
						margin-left: 16px;
					}
				}

				&--text {
					font-size: 14px;
					line-height: 1.5;
					letter-spacing: 0.08px;

  				a {
  					color: #2b84eb;
  				}
				}

				&--submit {
					padding-top: 16px;
				}
			}

			&__input {
				padding: 13px 24px;
				font-size: 24px;
				font-weight: 600;
				line-height: 1.5;
				letter-spacing: 0.14px;
				border-radius: 3.5px;
				box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);
				background-color: #fff;
				border: 0 none;

				&.tac {
					text-align: center;
				}
			}

			&__submit {
				padding: 19px;
				min-width: 160px;
				font-size: 18px;
				font-weight: 600;
				color: #fff;
				line-height: 1.5;
				letter-spacing: 0.11px;				
				border-radius: 500px;
				background-color: #2b84eb;

  			&:disabled {
 					background-color: #dbdfe4;
  			}
			}
		}
	}
}
/*Forms*/
/*Basic*/

/*Content*/
.page {
	&__wrapper {
		@include flex(column, center, stretch, false);
		min-height: 100vh;
	}

	&__title {
		margin: 0;
		font-size: 24px;
		font-weight: 600;
		line-height: 1.5;
		letter-spacing: 0.14px;
	}
}

.content {
	&__wrapper {
		flex: 1 0 auto;
		@include flex(row-reverse, false, stretch, false);

		& > .content {
			flex: 1 0 auto;
			@include flex(column, flex-start, stretch, false);
			max-width: calc(100% - 350px);


			& > .content__block {
				flex: 1 0 auto;
				@include flex(column, flex-start, stretch, false);
				padding: 40px 45px 40px 30px;

				& > .section--fullheight {
					flex: 1 0 auto;

					.block--fullheight {
						display: flex;
						flex-direction: column;
						justify-content: center;
						height: 100%;

						.block__empty {
							flex: 1 0 auto;
							@include flex(column, center, center, false);
							width: 100%;
						}
					}

					.page--fullheight {
						display: flex;
						flex-direction: column;
						justify-content: center;
						height: 100%;
					}
				}
			}
		}
	}
}

.breadcrumbs {
	padding: 32px;
	background-color: #fff;
	border: solid 1px #f5f8f9;
	box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);
}

.block__title {
	margin-bottom: 16px;
	font-size: 18px;
	font-weight: 500;
	line-height: 1.5;
	letter-spacing: 0.11px;

	&--schedule {
		@include flex(false, space-between, center, wrap);

		.date {
			font-size: 24px;
			font-weight: 600;
			line-height: 1.5;
			letter-spacing: 0.14px;
		}

		.count {
			font-size: 24px;
			font-weight: normal;
			line-height: 1.5;
			letter-spacing: 0.14px;
		}

		.slashes {
			flex: 0 0 100%;
			padding-top: 16px;
			@include flex(false, space-between, center, false);
			height: 3px;

			.slash {
				display: block;
				margin: 0 9px;
				width: 100%;
				height: 3px;
				background-color: #dbdfe4;

				&.missed {
					background-color: #ff765f;
				}

				&.taken {
					background-color: #2b84eb;
				}

				&:first-child {
					margin-left: 0;
				}

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	img {
		margin-right: 8px;
		vertical-align: middle;
	}
}

/*SignIn*/
.signin {
	.content__wrapper > .content {
		max-width: unset;
		@include flex(false, center, center, false);
	}
}
/*SignIn*/

/*Empty*/
.empty {
	&__icon {
		margin-bottom: 31px;
	}

	&__title {
		margin-bottom: 8px;
		font-size: 18px;
		font-weight: 600;
		line-height: 1.5;
		letter-spacing: 0.11px;
	}

	&__text {
		margin: 0 auto 40px auto;
		max-width: 300px;
		font-size: 14px;
		font-weight: 500;
		text-align: center;
		line-height: 1.5;
		letter-spacing: 0.08px;
	}

	&__btns {
		.btns {
			padding: 18px 50px;
			font-size: 18px;
			font-weight: 600;
			line-height: 1.5;
			letter-spacing: 0.11px;
			color: #fff;
			border-radius: 500px;
			box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);
			background-color: #2b84eb;
		}
	}
}
/*Empty*/

/*Family*/
.family {
	&__block {
		margin-bottom: 24px;

		.family {
			&__list {
				.list {
					&__item {
						display: inline-block;
						margin-right: 40px;
						vertical-align: top;
						opacity: 0.5;

						&.active {
							opacity: 1;

							.family {
								&__avatar {
									border-color: #2b84eb;
								}

								&__name {
									padding-top: 0;

									span {
										font-size: 18px;
	  								letter-spacing: 0.11px;
									}
								}
							}
						}
					}

					&__link {
						position: relative;
					}
				}
			}

			&__avatar {
				margin: 0 auto 16px;
				width: 64px;
				height: 64px;
				border: 4px solid transparent;
			}

			&__name {
				display: block;
				padding-top: 6px;

				span {
					display: block;
					font-size: 14px;
					font-weight: 500;
					line-height: 1.5;
					letter-spacing: 0.08px;
					text-align: center;
				}
			}

			&__notification {
				position: absolute;
				top: 0;
				right: 0;
				width: 24px;
				height: 24px;
				padding: 1px 8px;
				font-size: 14px;
				line-height: 1.57;
				letter-spacing: -0.41px;
				text-align: center;
				color: #fff;  
				border-radius: 50%;
				background-color: #ff765f;
			}			
		}
	}

	&__page {
		.block__title {
			@include flex(false, space-between, flex-end, false);
		}

		.btn-invite,
		.btn-remove {
			padding: 19px;
			min-width: 260px;
			font-size: 18px;
			font-weight: 500;
			font-stretch: normal;
			font-style: normal;
			line-height: 1.5;
			letter-spacing: 0.11px;
			text-align: center;
			color: #fff;			  
			border-radius: 5px;

		}

		.btn-invite {
			background-color: #2b84eb;
		}

		.btn-remove {
			background-color: #ff765f;
		}

		.courses__block {
			@include flex(false, space-between, stetch, false);

			.tabs__wrapper {
				flex: 0 0 430px;
				margin-right: 30px;
				width: 430px;

			}

			.courses__list .card .course__date .notifications {
				display: inline-block;
				padding: 4px 8px;
				font-size: 11px;
				font-weight: 500;
				letter-spacing: 0.07px;
				color: #fff;
				border-radius: 500px;
				background-color: #ff765f;
			}
		}

		.btns__wrapper {
			position: relative;

