/*Calendar*/
.calendar {
	&__block {
		margin-bottom: 24px;
		padding: 24px 0;
		border-top: 1px solid #dbdfe4;
		border-bottom: 1px solid #dbdfe4;

		.calendar {
			&__months {
				.months {
					&__list {
						@include flex(false, space-between, center, false);
						margin-bottom: 14px;

						.month {
							font-size: 18px;
							line-height: 1.5;
							letter-spacing: 0.11px;

							&.active {
								font-size: 24px;
								font-weight: 600;
								letter-spacing: 0.14px;
							}
						}					
					}
				}
			}

			&__days {
				.days {
					&__list {
						@include flex(false, space-between, center, false);

						.day {
							text-align: center;

							.month__day {
								margin-bottom: 8px;
								padding: 20px;
								width: 62px;
								height: 62px;
								font-size: 24px;
								font-weight: 500;
								line-height: 0.92;
								letter-spacing: -0.41px;
								border-radius: 50%;
							}

							.week__day {
								font-size: 18px;
								line-height: 1.5;
								letter-spacing: 0.11px;
							}

							&.active {
								.month__day {
									font-weight: 700;
									color: #fff;
									background-color: #2b84eb;
								}
							}

							&.missed {
								.month__day {
									color: #ff765f;
								}
							}

							&.past {
								opacity: 0.5;
							}
						}
					}
				}
			}
		}
	}
}
