/*Courses*/
.courses {
	&__list {
		.list {
			&__item {
				margin-bottom: 16px;

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		.card {
			padding: 16px 17px 20px 16px;
			min-width: 244px;
			border-radius: 5px;
			background-color: #fff;

			&__top {
				@include flex(false, space-between, center, false);
				margin-bottom: 8px;
			}

			.course {
				&__date {
					@include flex(false, space-between, center, false);
					margin-bottom: 8px;
					font-size: 11px;
					font-weight: 500;
					line-height: 1.5;
					letter-spacing: 0.07px;

					.notifications {
						color: #ff765f;
					}
				}

				&__name {
					font-size: 18px;
					font-weight: 600;
					line-height: 1.5;
					letter-spacing: 0.11px;
				}	

				&__counter {
					margin-left: 16px;
					font-size: 18px;
					font-weight: 500;
					line-height: 1.5;
					letter-spacing: 0.11px;
				}

				&__slashes {
					@include flex(false, space-between, center, false);
					height: 3px;

					.slash {
						display: inline-block;
						margin: 0 3px;
						width: 100%;
						height: 3px;
						background-color: #dbdfe4;

						&.taken {
							background-color: #2b84eb;
						}

						&.missed {
							background-color: #ff765f;
						}

						&:first-child {
							margin-left: 0;
						}

						&:last-child {
							margin-right: 0;
						}
					}

					&--percent {
						position: relative;
						margin: 0;
						height: 3px;
						background-color: #dbdfe4;

						.slash {
							position: absolute;
							top: 0;
							left: 0;
							margin: 0;
						}
					}
				}	

				&__duration {
					margin-bottom: 8px;
					font-size: 11px;
					font-weight: 500;
					letter-spacing: 0.07px;
				}

				&__consumption {
					font-size: 18px;
					font-weight: 600;
					letter-spacing: 0.11px;
				}
			}

			&.past {

				.course {
					&__date {
						.notifications {
							color: #0d2a4a;
						}
					}

					&__name,
					&__counter {
						text-decoration: line-through;
					}

					&__slashes {
						.slash {
							&.missed,
							&.taken {
								background-color: #0d2a4a;
							}
						}
					}
				}
			}
		}

		&--horizontal {
			display: flex;

			.list {
				&__item {
					margin-right: 16px;
				}
			}
		}
	}

	&__page {
		position: relative;

		.btn-create {
			position: absolute;
			top: 0;
			right: 0;
			padding: 19px 23px 19px 22px;
