								  			}		  						
							  			}

							  			&--timeAdoze {
							  				@include flex(false, flext-start, center, false);
							  			}			
										}										
									}

									&--duration {
										position: relative;
										
										input {
											text-align: center;
										}

										.btn-prev,
										.btn-next {
