											bottom: 16px;
											width: 24px;
											height: 24px;
										  background-image: url("../img/icons/i-chevron_down.png");
											background-image: -webkit-image-set(
												url("../img/icons/i-chevron_down.png") 1x,
												url("../img/icons/i-chevron_down@2x.png") 2x,
												url("../img/icons/i-chevron_down@3x.png") 3x,
