										.btn-prev {
											left: 16px;
											transform: rotate(90deg);
										}

										.btn-next {
											right: 16px;
											transform: rotate(-90deg);
										}
									}

									&--days {
										@include flex(false, space-between, center, false);

										input {
											display: none;

											&:checked + label {
												background-color: #fff;
												opacity: 1;
											}
