				}				
			}	
		}
	}
}
/*Settings*/
/*Content*/

/*SideBar*/
.sidebar {
	padding: 24px 32px;
	flex: 0 0 315px;
	margin-left: 45px;
	width: 315px;
	box-shadow: 2px 0 16px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  z-index: 1;

  &__header {
  	margin-bottom: 32px;
		font-size: 36px;
		font-weight: 700;
		line-height: 1.5;
		letter-spacing: 0.22px;
		text-align: center;
		color: #2b84eb;
  }

  &__nav {	
  	position: -webkit-sticky;	
  	position: sticky;
  	top: 0;
  	bottom: 0;
  	padding: 32px 0 64px 0;

  	.menu {
  		&__list {
  			list-style: none;

  			&--main {
  				margin-bottom: 80px;

  				.menu {

  					&__item {
  						position: relative;

  						.notification	{
  							display: none;
  							position: absolute;
								width: 100px;
								height: 85px;
								top: 50%;
								right: -32px;
								margin-top: -43px;

								&.family {
									background-image: url("../img/icons/i-family_menu.png");
									background-image: -webkit-image-set(
										url("../img/icons/i-family_menu.png") 1x,
										url("../img/icons/i-family_menu@2x.png") 2x,
										url("../img/icons/i-family_menu@3x.png") 3x,
									);
									background-image: image-set(
										url("../img/icons/i-family_menu.png") 1x,
										url("../img/icons/i-family_menu@2x.png") 2x,
										url("../img/icons/i-family_menu@3x.png") 3x,
									);
								}
  						}

  						&.family {
  							opacity: 0;
  							transition: opacity 0.5s ease;
  						}

  						&.show-notification {
  							opacity: 1;


  							.notification {
  								display: block;
  							}
  						}
  					}
						&__link {
							display: block;
							margin-bottom: 7px;
							padding: 16px 21px;
							font-size: 18px;
							line-height: 1.5;
							letter-spacing: 0.11px;
							text-transform: uppercase;

							.icons {
								margin: -2px 26px 0 0;
								background-image: url('/img/icons/i-menu.png');
								background-repeat: no-repeat;

								&.i-home {
									background-position: -160px -0px;
								}					

								&.i-clipboard {
									background-position: -32px -0px;
								}

								&.i-medicine	{
									background-position: -224px -0px;
								}

								&.i-users	{
									background-position: -288px -0px;
									opacity: 0.6;
								}

								&.i-gear {
									background-position: -96px -0px;
								}
							}

							&:hover,
							&.active {
								font-weight: 600;
								background-color: #fff;
								border-radius: 5px;
								box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);

								.i-home {
									background-position: -128px -0px;
								}

								.i-clipboard {
									background-position: 0px -0px;
								}


								.i-medicine {
									background-position: -192px -0px;
								}					

								.i-users {
									background-position: -256px -0px;
									opacity: 1;
								}					

								.i-gear {
									background-position: -64px -0px;
								}										
							}
						}  					
  				}
  			}

  			&--last {
  				position: -webkit-sticky;
  				position: sticky;
  				bottom: 0;

  				.menu {
  					&__link {
  						display: block;
							margin-bottom: 7px;
							padding: 16px 21px;
							font-size: 18px;
							color: #ff765f;
							line-height: 1.5;
							letter-spacing: 0.11px;
							text-align: center;
							text-transform: uppercase;
  					}
  				}
  			}
  		}
  	}
  }
}
/*SideBar*/

/*Footer*/
.footer {
  background-color: #fff;
	box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);
  border: solid 1px #f5f8f9;
  z-index: 3;

	&__content {
		padding: 7px 45px;
	}

	&__menu {
		.menu {
			&__list {
				list-style: none;
				text-align: right;
			}

			&__item {
				display: inline-block;
				padding: 0px 42px;
				border-right: 1px solid #0d2a4a;

				&:last-child {
					padding-right: 0;
					border-right: 0 none;
				}
			}

			&__link {
				font-size: 14px;
				letter-spacing: 0.08px;
				text-decoration: none;
			}
		}
	}
}
/*Footer*/

/*Popups*/
.popup {
	&__overlay {
		@include flex(column, center, center, false);
		display: none;
		position: fixed;
		top: 0; right: 0; 
		bottom: 0; left: 0;
		-webkit-backdrop-filter: blur(5px);
		backdrop-filter: blur(5px);
  	background-color: rgba(43, 132, 235, 0.05);
		z-index: 40;
	}

	&__block {
		position: relative;
		display: none;
		width: 490px;
		max-height: 80vh;
		padding: 24px 55px 36px 55px;
		border-radius: 20px;
		box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);
		background-color: #fff;
	  z-index: 44;
	  overflow-y: auto;

	  .block {
	  	&__top {
		  	text-align: right;

		  	.btn-close {
		  		width: 40px;
		  		height: 40px;
		  		background: url("../img/icons/i-close_red.png");
		  	}
		  }

		  &__title {
		  	margin-bottom: 24px;
				font-size: 24px;
				font-weight: 600;
				line-height: 1.5;
				letter-spacing: 0.14px;

				&.tac {
					text-align: center;
				}
		  }

		  &__content {
		  	h1 {
					margin-bottom: 16px;
					font-size: 36px;
					font-weight: 700;
					line-height: 1.5;
					letter-spacing: 0.22px;
		  	}

		  	h2 {
					margin-bottom: 16px;
					font-size: 24px;
					font-weight: 700;
					line-height: 1.5;
					letter-spacing: 0.14px;
		  	}

		  	h3 {
					margin-bottom: 16px;
					font-size: 18px;
					font-weight: 700;
					line-height: 1.5;
					letter-spacing: 0.11px;
		  	}

		  	p {
					margin-bottom: 32px;
					font-size: 18px;
					font-weight: 500;
					line-height: 1.5;
					letter-spacing: 0.11px;
		  	}
		  }

		  &__btns {
		  	@include flex(false, space-between, center, false);

		  	.btns {
					padding: 18px 0;
					min-width: 160px;
					font-size: 18px;
					font-weight: 600;
					letter-spacing: 0.11px;
					color: #fff;	
					text-transform: uppercase;
					text-align: center;
					border-radius: 500px;
		  	}

		  	.btn-cancel {
					background-color: #0d2a4a;
		  	}

		  	.btn-remove {
		  		background-color: #ff765f;
		  	}

		  	.btn-invite {
		  		background-color: #2b84eb;
		  	}

		  	&.tac {
		  		justify-content: center;
		  	}
		  }
	  }

	  &.tac {
	  	text-align: center;
	  }

		&.popup--terms {
			width: 825px;
		}

		&.popup--medicine{
			padding: 24px;
			width: 459px;

			.medicine {
				&__gallery {
					@include flex(false, space-between, start, false);
					margin-bottom: 8px;
					
					.small {
						margin-bottom: 12px;
						opacity: 0.5;

						&.active{
							opacity: 1;
						}
					}
					
					.image {
						border-radius: 10px;
					}
				}

				&__time {
					margin-bottom: 16px;
					font-size: 14px;
					font-weight: 500;
					line-height: 1;
					letter-spacing: 0.08px;
				}

				&__name {
					font-size: 24px;
					font-weight: 600;
					line-height: 1;
					letter-spacing: 0.14px;
				}

				&__quantity {
					font-size: 14px;
					line-height: 1.5;
					letter-spacing: 0.08px;
				}

				&__btns {
					padding-top: 20px;

					&.tac {
						text-align: center;
					}

					.btn-close,
					.btn-take,
					.btn-untake {
						margin: 0 15px;
						min-width: 159px;
						padding: 18px 48px;
						font-size: 18px;
						font-weight: 600;
						line-height: 1.5;
						letter-spacing: 0.11px;
						text-align: center;
						color: #fff;						  
						border-radius: 500px;
					}

					.btn-close {
						background-color: #0d2a4a;
					}

					.btn-take {
						background-color: #2b84eb;
					}

					.btn-untake {
						background-color: #ff765f;
					}

					.btn-takeswipe {
						position: relative;
						display: block;
						padding: 4px;
						width: 100%;
						height: 72px;
						border-radius: 500px;
						box-shadow: inset 1px 1px 2px 0 rgba(0, 0, 0, 0.1);
						background-color: #dbdfe4;

						span {
							display: inline-block;
							position: absolute;
							top: 4px;
							left: 4px;
							padding: 18px 40px;
							font-size: 18px;
							font-weight: 600;
							line-height: 1.5;
							letter-spacing: 0.11px;
							text-align: center;
							color: #fff;
							white-space: nowrap;
							border-radius: 500px;
							background-color: #2b84eb;
							transition: all 0.5s ease;

							&:after {
								content: '';
								display: inline-block;
								margin-left: 16px;
								width: 13px;
								height: 13px;
								background: url("../img/icons/i-arrow_right-white.png") no-repeat 50%;
							}
						}

						&:active {
							span {
								left: calc(100% - 163px);
							}
						}
					}
				}
			}
		}

		&.popup--missed {
			.medicine {
				&__name,
				&__quantity {
					color: #ff765f;
				}
			}
		}

		&.popup--taken {
			.medicine {
				&__name,
				&__quantity {
					text-decoration: line-through;
				}
			}
		}

		&--ping {
			.form__input--pin {

			}
		}
	}

	&.show .popup__overlay {
		display: flex;
	}

	&.phone .popup--phone,
	&.sms .popup--sms,
	&.terms .popup--terms,
	&.medicine .popup--medicine,
	&.missed .popup--missed,
	&.taken .popup--taken,
	&.take .popup--take,
	&.invite .popup--invite,
	&.remove .popup--remove,
	&.pin .popup--pin {
		display: block;
	} 
}
