/*Buttons*/
.btns {
	display: inline-block;
	cursor: pointer;
	text-decoration: none;

	&.btn-whitebrd {
		padding: 14px 60px 14px 25px;
		width: 100%;
		font-size: 16px;
		font-weight: 600;
		line-height: 1.5;
		letter-spacing: 0.1px;
		text-align: left;
		color: #2b84eb;
		background-color: #fff;
		background-image: url("../img/icons/i-plus_blue.png");
		background-image: -webkit-image-set(
			url("../img/icons/i-plus_blue.png") 1x,
			url("../img/icons/i-plus_blue@2x.png") 2x,
			url("../img/icons/i-plus_blue@3x.png") 3x,
		);
		background-image: image-set(
			url("../img/icons/i-plus_blue.png") 1x,
			url("../img/icons/i-plus_blue@2x.png") 2x,
			url("../img/icons/i-plus_blue@3x.png") 3x,
		);
		background-position: 	calc(100% - 16px) 50%;
		background-repeat: no-repeat;
		border: solid 1px #dbdfe4;
		border-radius: 3.5px;
		box-shadow: 0 7px 20px 0 rgba(216, 223, 225, 0.5);

		&:before {
			display: inline-block;
			content: '';
			margin-right: 16px;
			width: 44px;
			height: 44px;
			border-radius: 500px;
