		}		
	}

	&.btn-icon {
		&.btn-course {
			&:before {
				background-image: url("../img/icons/i-clipboard_circle.png");
				background-image: -webkit-image-set(
					url("../img/icons/i-clipboard_circle.png") 1x,
					url("../img/icons/i-clipboard_circle@2x.png") 2x,
					url("../img/icons/i-clipboard_circle@3x.png") 3x,
				);
				background-image: image-set(
					url("../img/icons/i-clipboard_circle.png") 1x,
