					url("../img/icons/i-clipboard_circle@3x.png") 3x,
				);						
			}
		}

		&.btn-notes {
			&:before {
				background-image: url("../img/icons/i-notes_circle.png");
				background-image: -webkit-image-set(
					url("../img/icons/i-notes_circle.png") 1x,
					url("../img/icons/i-notes_circle@2x.png") 2x,
					url("../img/icons/i-notes_circle@3x.png") 3x,
				);
				background-image: image-set(
					url("../img/icons/i-notes_circle.png") 1x,
					url("../img/icons/i-notes_circle@2x.png") 2x,
					url("../img/icons/i-notes_circle@3x.png") 3x,
				);						
			}
		}

		&.btn-stock {
			&:before {
				background-image: url("../img/icons/i-stock.png");
				background-image: -webkit-image-set(
					url("../img/icons/i-stock.png") 1x,
					url("../img/icons/i-stock@2x.png") 2x,
					url("../img/icons/i-stock@3x.png") 3x,
				);
				background-image: image-set(
					url("../img/icons/i-stock.png") 1x,
					url("../img/icons/i-stock@2x.png") 2x,
					url("../img/icons/i-stock@3x.png") 3x,
